import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import swal from "sweetalert";
import { throttle } from 'lodash';

import FormField from "../../components/reuseable/FormField";
import {
  getAllcatalogue,
  getAllfactory,
  saveData,
  updateSold,
  getAllrating
} from "../../store/catalogueSlice";
const AddForm = () => {
  const { factory_list, unsold, catalogue_data ,rating_list} = useSelector(
    (state) => state.catalogue
  );
  const usersString = localStorage.getItem("users");
  const usersd = JSON.parse(usersString);
  const [users, setUsers] = useState([]);
  const dispatch = useDispatch();
  const [formErrors, setFormErrors] = useState({});
  const validateForm = () => {
    const errors = {};
  
    // Perform validation for each field
    if (!catalogueData.lot) {
      errors.lot = "Lot is required.";
    }
    if (!catalogueData.invoice) {
      errors.invoice = "Invoice is required.";
    }
    if (!catalogueData.factory) {
      errors.factory = "Factory is required.";
    }
    if (!catalogueData.grade) {
      errors.grade = "Grade is required.";
    }  if (!catalogueData.net_weight) {
      errors.net_weight = "Weight is required.";
    }  if (!catalogueData.price) {
      errors.price = "Rate is required.";
    }  if (!catalogueData.pkgs) {
      errors.pkgs = "Pkgs is required.";
    }  if (!catalogueData.totalkg) {
      errors.totalkg = "Total is required.";
    }  if (!catalogueData.warehouse) {
      errors.warehouse = "Warehouse is required.";
    }  if (!catalogueData.category) {
      errors.category = "Category is required.";
    }  if (!catalogueData.sale_no) {
      errors.sale_no = "Sale No is required.";
    }
    if (!catalogueData.sample_c) {
      errors.sample_c = "Sample is required.";
    }
    if (!catalogueData.gross_total) {
      errors.gross_total = "Gross Total is required.";
    }
    if (!catalogueData.lrating) {
      errors.lrating = "leaf rating is required.";
    }
    if (!catalogueData.lirating) {
      errors.lirating = "liqour rating is required.";
    }
    if (!catalogueData.gross_bag) {
      errors.gross_bag = "liqour rating is required.";
    }

    if (!catalogueData.buyer) {
      errors.gross_bag = "buyer required.";
    }
    if (!catalogueData.maximum_bid) {
      errors.gross_bag = "maximum_bid is required.";
    }
    // ... perform validation for other fields ...
  
    return errors;
  };
  
  const gradeOptions = [
    { value: "BOP", label: "BOP" },
    { value: "GBOP", label: "GBOP" },
    { value: "GOF", label: "GOF" },
    { value: "OF", label: "OF" },
    { value: "FOF", label: "FOF" },
    { value: "PF", label: "PF" },
    { value: "RD", label: "RD" },
    { value: "DUST", label: "DUST" },
    { value: "CD", label: "CD" },
  ];

  const categoryOptions = [
    { value: "DUST", label: "DUST" },
    { value: "LEAF", label: "LEAF" },
    { value: "SUPPLIMENT", label: "SUPPLIMENT" },
  ];
  const [catalogueData, setCatalogueData] = useState({
    lot: "",
    invoice: "",
    factory: "",
    grade: "",
    net_weight: "",
    gross_bag:"",
    sample_c: "",
    gross_total: "",
    price: "",
    pkgs: "",
    totalkg: "",
    brokers: usersd.link_id,
    warehouse: "",
    lrating: "",
    lirating: "",
    category: "",
    sale_no: "",
    buyer: "",
    maximum_bid: "",
    posted: usersd.username,
  });

  const getUserData = (event) => {
    const name = event.target.name;
    const value = event.target.value;
  
    if (name === "pkgs") {
      setCatalogueData((prevState) => ({
        ...prevState,
        [name]: value,
      }));
      getTotal(value);

    } else if(name==="sample_c"){
      setCatalogueData((prevState) => ({
        ...prevState,
        [name]: value,
      }));
      getgrossTotal(value);
    }else {
      setCatalogueData((prevState) => ({
        ...prevState,
        [name]: value,
      }));
    }
  };

  const getgrossTotal = (value) => {
    console.log(`888`);
    const sample = parseFloat(catalogueData.sample_c);
    const nettotalKg =  parseFloat(catalogueData.gross_total);
   const grossTotal = nettotalKg- sample;
    setCatalogueData((prevState) => ({
      ...prevState,
      
      gross_total:grossTotal,
    }));
  };
  const getTotal = (value) => {
    const netWeight = parseFloat(catalogueData.net_weight);
    const totalKg = isNaN(netWeight) ? 0 : netWeight * value;
    let deduction;
  
    if (catalogueData.category === 'LEAF') {
      deduction = 1.5;
    } else if (catalogueData.category === 'DUST') {
      deduction = 0.8;
    } else {
      deduction = 0.5;
    }
    const grossTotal = totalKg - deduction;
  
    console.log(deduction);
    setCatalogueData((prevState) => ({
      ...prevState,
      totalkg: totalKg,
      sample_c:deduction,
      gross_total:grossTotal,
    }));
  };
  
    
  const buyer = factory_list.filter((buyer) => buyer.type === "BIDDER");
  const warehouse = factory_list.filter((ware) => ware.type === "WAREHOUSE");
  const factory = factory_list.filter((factory) => factory.type === "FACTORY");
const leafrating = rating_list.filter((rate) => rate.type === "LEAF");
const liquorrating = rating_list.filter((rate) => rate.type === "LIQUOR");
  const addAccounts = async (e) => {
    const errors = validateForm();
    if (Object.keys(errors).length > 0) {
      setFormErrors(errors);
      return;
    }
    e.preventDefault();
     await dispatch(saveData(catalogueData));
      setCatalogueData({
        lot: "",
        invoice: "",
        factory: "",
        grade: "",
        net_weight: "",
        gross_bag:"",
        sample_c: "",
        gross_total: "",
        price: "",
        pkgs: "",
        totalkg: "",
        brokers: usersd.link_id,
        warehouse: "",
        lrating: "",
        lirating: "",
        category: "",
        sale_no: "",
        buyer: "",
        maximum_bid: "",
        posted: usersd.username,
      });
      dispatch(getAllcatalogue());
      swal({
        title: "Success",
        text: "Catalogue created successfully!!",
        icon: "success",
        timer: 2500,
        button: false,
      });
  };

  const updateAccount = (e) => {
    const errors = validateForm();
    if (Object.keys(errors).length > 0) {
      setFormErrors(errors);
      return;
    }
    if (unsold) {
      e.preventDefault();
      dispatch(updateSold(catalogueData));
      swal({
        title: "Success",
        text: "Catalogue updated successfully!!",
        icon: "success",
        timer: 2500,
        button: false,
      });
      setCatalogueData({
        lot: "",
        invoice: "",
        factory: "",
        grade: "",
        net_weight: "",
        gross_bag:"",
        sample_c: "",
    gross_total: "",
        price: "",
        pkgs: "",
        totalkg: "",
        brokers: usersd.link_id,
        warehouse: "",
        lrating: "",
        lirating: "",
        category: "",
        sale_no: "",
        buyer: "",
        maximum_bid: "",
        posted: usersd.username,
      });
      dispatch(getAllcatalogue());
    }
  };
 
  // const throttledSetCatalogueData = throttle((data) => {
  //   setCatalogueData(data);
  // }, 500); // Adjust the throttle delay as per your requirements
  useEffect(() => {
 dispatch(getAllfactory());
 dispatch(getAllrating());
  }, [dispatch]);
  useEffect(() => {
  
    if (unsold) {
      console.log(catalogue_data[0]);
      setCatalogueData(catalogue_data[0]);
    }
  }, [unsold]);

  return (
    <>
      <section className="col-lg-7 connectedSortable">
        <div className="box box-danger">
          {/* Tabs within a box */}
          <div className="tab-content no-padding">
            {/* Morris chart - Sales */}
            <div className="box-header header-custom">
              <h3 className="box-title">Unsold Item</h3>
            </div>
            <div className="box-body form-horizontal">
              <div className="row">
            

                <div className="col-md-6">
                
                        <FormField
                    label="Buyer"
                    name="buyer"
                    value={catalogueData.buyer}
                    type="select"
                    options={buyer.map((shead) => ({
                      value: shead.id,
                      label: shead.name,
                    }))}
                    onChange={getUserData}
                    error={formErrors.warehouse} 
                  />      <FormField
                  label="Sell Price"
                  name="maximum_bid"
                  value={catalogueData.maximum_bid}
                  type="text"
                  onChange={getUserData}
                  error={formErrors.maximum_bid} 
                  />

                 
                </div>
              </div>
            </div>

            {/* /.box-body */}
            <div className="box-footer">
              <button type="submit" className="btn btn-danger">
                Cancel
              </button>
              <input
                type="button"
                name="save"
                className="btn btn-success pull-right"
                defaultValue={unsold ? "Update" : "Save"}
                id="butsave"
                onClick={unsold ? updateAccount : addAccounts}
              />
            </div>
            {/* /.box-footer */}
          </div>
        </div>
      </section>
    </>
  );
};

export default AddForm;
